
.contador {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin: auto;
    text-align: center;
  
    .time-wrapper {
      display: flex;
      gap: 20px;
  
      .time-box {
        min-width: 75px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #141414;
        padding: 20px;
        border-radius: 8px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  
        h2 {
          font-size: 2rem;
          margin: auto;
          margin-left: 25px;
          color: #f7fc00;
          letter-spacing: 1rem;
        }
  
        span {
          font-size: 1rem;
          color: #e2e2e2;
        }
      }
    }
  
    h2 {
      font-size: 2rem;
      color: #ff4500;
    }
  }
  

  @media (max-width: 550px) {
    .contador {
      .time-wrapper {
        display: flex;
        width: 100%;
        
      
        .time-box {
          margin: auto;
          padding: 2px;
          left: 0;
          h2 {
            margin: auto;
            font-size: 1rem;
            text-align: center;
            letter-spacing: normal;
          }
          span {
            font-size: .8rem;
            margin-top: 5px;
            margin-bottom: 10px;
          }
        }
        
      }
    }
  }