.secundaryPage {
    width: 100%;
    height: 450px;
    display: flex;
    background-color: rgb(37, 37, 37);
}

.containerSecundaryPage {
    width: 60%;
    height: auto;
    margin: auto;
    margin-bottom: 125px;
    margin-top: -300px;
    background-color: transparent;

    h2 {
        color: whitesmoke;
        font-size: 2rem;
        letter-spacing: 2px;
    }

    h3 {
        color: wheat;
        font-size: 1.2rem;
    }


    img {
        width: 100%;
        height: 550px;
        margin-top: 25px;
        border-radius: 10px;
    }

    .container {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-between;

        .widgets {
            width: 30%;
        }

        .content {
            width: 60%;
            height: auto;
            display: inline-block;
            margin-top: 0;

            h2 {
                color: black;
            }
            p {
                color: black;
                font-size: 1.2rem;
                line-height: 2rem;
            }

        }
    }

}

@media (max-width: 1440px) {
    
    .containerSecundaryPage {
        width: 70%;
        margin-top: -350px;
    
        h2 {
            font-size: 1.6rem;
        }
    
        h3 {
            font-size: 1rem;
            font-weight: 300;
        }
    
    
        img {
            height: 450px;
        }
    }
}

@media (max-width: 550px) {
    .secundaryPage {}
        .containerSecundaryPage {
            width: 80%;
            margin-top: -350px;

            .container {
                display: inline;
                width: 90%;

                .content {
                    width: 100%;
                }
            }
        
            h2 {
                font-size: 1.3rem;
            }
        
            h3 {
                font-size: 1rem;
                font-weight: 300;
            }
        
        
            img {
                height: 250px;
            }
        }
    
}